.carousel-item{
    height: 100vh;
     min-height: 400px;
     background: no-repeat scroll scroll scroll;
     -webkit-background-size: cover;
     background-size: cover;
}
.carousel-caption{
    bottom: 150px;
    padding-right: 50%;
}
.carousel-caption h2{
    font-size: 60px;
    letter-spacing: 1px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    animation: slide_up 1.5s ease;
}
.carousel-caption p{
    margin: auto;
    font-size: 18px;
    font-family: 'Courier New', Courier, monospace;
    animation: slide_up 1.5s ease;
}
.carousel-caption a{
  border: 2px solid #BBBBBB;
  color:#BBBBBB;
  padding: 14px 28px;
  font-size: 18px;
  cursor: pointer;
  font-weight: 500;
  animation: slide_up 1.5s ease;
}
.carousel-caption a:hover{
  text-decoration: none;
  background-color:  #BBBBBB;;
  color: black;
}
@keyframes slide_up {
    0%{
        transform: translateY(250px);
    }
    100%{
        transform: rotateY(0);
    }    
}

@media (max-width:1050px){
    .carousel-caption a{
        border: 2px solid #BBBBBB;
        color:#BBBBBB;
        padding: 10px 18px;
        font-size: 13px;
        cursor: pointer;
        font-weight: 200;
        animation: slide_up 2.1s ease;
      }
      .carousel-caption a:hover{
        text-decoration: none;
        background-color:  #BBBBBB;;
        color: black;
      }
}


