.navbar-nav a{
    font-family: 'Courier New', Courier, monospace;
    font-size: 18px;
    text-transform: uppercase;
}
.navbar-light .site-logo{
    
    font-size: 25px;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
    font-weight: 900;  
    color: var(--text);
    text-decoration: none;
      
}
.navbar-light .navbar-nav .active>.nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show>.nav-link {
    color: var(--text);
}
.navbar-light .navbar-nav .nav-link{
    color: var(--text);
}
.navbar-nav{
    text-align: center;
}
.nav-link{
    padding: .2rem 1rem;
}
.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover{
    color: black;
}

header {
    --text: #f4f4f4;
    --text-inverse:  #f4f4f4;
    --background: transparent;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2em 3em;
    transition: background 250ms ease-in;
    background: var(--background);
    color: var(--text);
  }
  
  .nav__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  .nav-link {
    text-decoration: none;
    color: inherit;
    display: inline-block;
    position: relative;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 0.9rem;
  }
  .nav-scrolled {
    --text: #333;
    --text-inverse: #f4f4f4;
    --background: #f4f4f4;
    box-shadow: 0 3px 20px rgba(0, 0, 0, 0.2);
  }
  
  .carousel .slide {
    padding: 50vh 0;
    background-size: cover;
    background-blend-mode: multiply;
    color: white;
    text-align: center;
  }
@media only screen and (max-width:767px){
 .w-100{
     height: 80vh;
     max-width:100%;
 }
.navbar-toggler{
padding: 1px 5px;
font-size: 18px;
line-height: 0.3px;
background: #fff;
    }
}





